
'use client';

import React, { useEffect, useState, useTransition, useActionState, useCallback } from 'react';
import { useFormStatus } from 'react-dom';
import { useRouter } from 'next/navigation';
import { getSessionUser, getUsersAction, adminAddUserAction, adminUpdateUserAction, adminDeleteUserAction } from '@/lib/actions';
import type { User, AdminAddUserFormState, AdminUpdateUserFormState } from '@/lib/types';
import { Button, buttonVariants } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger, DialogClose } from "@/components/ui/dialog";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from "@/components/ui/alert-dialog";
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Loader2, AlertTriangle, Home, Users, UserPlus, Edit, Trash2, ShieldAlert, ServerCrash } from 'lucide-react';
import { useLoading } from '@/contexts/loading-context';
import { useToast } from '@/hooks/use-toast';
import UserAvatar from '@/components/user-avatar';

function DialogSubmitButton({ children, disabled }: { children: React.ReactNode, disabled?: boolean }) {
  const { pending } = useFormStatus();
  return <Button type="submit" disabled={pending || disabled}>{pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}{children}</Button>;
}

export default function ManageUsersPage() {
  const router = useRouter();
  const { startLoading, stopLoading, isLoading: isGlobalLoading } = useLoading();
  const { toast } = useToast();

  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [isLoadingUser, setIsLoadingUser] = useState(true); 
  const [users, setUsers] = useState<User[]>([]);
  const [isLoadingUsers, setIsLoadingUsers] = useState(false); 
  const [error, setError] = useState<string | null>(null);

  const [isAddUserOpen, setIsAddUserOpen] = useState(false);
  const [isEditUserOpen, setIsEditUserOpen] = useState(false);
  const [userToEdit, setUserToEdit] = useState<User | null>(null);
  const [isDeleting, startDeleteTransition] = useTransition();
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [userToDelete, setUserToDelete] = useState<User | null>(null);
  const [adminPasswordForDelete, setAdminPasswordForDelete] = useState('');
  const [deleteError, setDeleteError] = useState<string | null>(null);

  const initialAddUserState: AdminAddUserFormState = { message: null, errors: {}, success: false };
  const [addUserFormState, addUserFormAction, isAddUserPending] = useActionState(
    async (prevState: AdminAddUserFormState, formData: FormData) => {
      startLoading();
      const result = await adminAddUserAction(prevState, formData);
      stopLoading();
      if (result.success) {
        toast({ title: "成功", description: result.message });
        setIsAddUserOpen(false);
        await fetchUsers(); 
      } else {
        toast({ title: "失败", description: result.message || "创建用户失败。", variant: "destructive" });
      }
      return result;
    },
    initialAddUserState
  );

  const initialUpdateUserState: AdminUpdateUserFormState = { message: null, errors: {}, success: false };
  const [updateUserFormState, updateUserFormAction, isUpdateUserPending] = useActionState(
    async (prevState: AdminUpdateUserFormState, formData: FormData) => {
      startLoading();
      const result = await adminUpdateUserAction(prevState, formData);
      stopLoading();
      if (result.success) {
        toast({ title: "成功", description: result.message });
        setIsEditUserOpen(false);
        setUserToEdit(null);
        await fetchUsers(); 
      } else {
        toast({ title: "失败", description: result.message || "更新用户失败。", variant: "destructive" });
      }
      return result;
    },
    initialUpdateUserState
  );
  
  const fetchUsers = useCallback(async () => {
    setIsLoadingUsers(true);
    // Not calling global startLoading here, as it's for the table content primarily
    try {
      const fetchedUsers = await getUsersAction();
      setUsers(fetchedUsers);
    } catch (err) {
      console.error("获取用户列表错误:", err);
      setError("加载用户列表时发生错误。");
      toast({ title: "错误", description: "无法加载用户列表。", variant: "destructive" });
    } finally {
      setIsLoadingUsers(false);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [toast]); // toast is stable

  const initialLoad = useCallback(async () => {
    setIsLoadingUser(true);
    startLoading(); // Global loading for initial page auth and setup
    try {
      const user = await getSessionUser();
      setCurrentUser(user);
      if (!user) {
        router.push('/login?redirect=/admin/manage-users');
        return; 
      }
      if (user.op !== 1) {
        // Non-admin access is handled by render logic below.
        // No need to fetch users if not admin.
        return;
      }
      await fetchUsers(); 
    } catch (err) {
      console.error("加载用户管理页面数据错误:", err);
      setError("加载页面数据时发生错误。");
    } finally {
      setIsLoadingUser(false);
      if (typeof window !== 'undefined' && window.location.pathname.startsWith('/admin/manage-users')) {
         stopLoading();
      }
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [router, fetchUsers]); // startLoading, stopLoading, toast removed as they are stable or handled

  useEffect(() => {
    initialLoad();
  }, [initialLoad]);

  const handleOpenEditDialog = (user: User) => {
    setUserToEdit(user);
    setIsEditUserOpen(true);
  };

  const confirmDeleteUser = (user: User) => {
    setUserToDelete(user);
    setAdminPasswordForDelete('');
    setDeleteError(null);
    setShowDeleteDialog(true);
  };

  const executeDeleteUser = async () => {
    if (!userToDelete || !adminPasswordForDelete) {
      setDeleteError("请输入您的账户密码以确认。");
      return;
    }
    setDeleteError(null);
    startLoading(); 
    startDeleteTransition(async () => {
      try {
        const result = await adminDeleteUserAction(userToDelete.id, adminPasswordForDelete);
        if (result.success) {
          toast({ title: '成功', description: result.message });
          setShowDeleteDialog(false);
          setUserToDelete(null);
          setUsers(prev => prev.filter(u => u.id !== userToDelete.id));
        } else {
          setDeleteError(result.message);
          toast({ title: '删除失败', description: result.message, variant: 'destructive' });
        }
      } catch (err: any) {
        setDeleteError(err.message || "删除用户时发生未知错误。");
        toast({ title: '错误', description: err.message || "删除用户时发生未知错误。", variant: 'destructive' });
      } finally {
        stopLoading(); 
      }
    });
  };


  if (isLoadingUser) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-muted-foreground">正在验证权限...</p>
      </div>
    );
  }

  if (!currentUser) { // Should be handled by redirect in initialLoad
     return (
      <div className="max-w-2xl mx-auto py-8 text-center">
        <Card className="shadow-xl">
          <CardHeader><CardTitle className="text-2xl font-bold">需要登录</CardTitle></CardHeader>
          <CardContent><Alert variant="destructive"><AlertTitle>请先登录</AlertTitle></Alert></CardContent>
        </Card>
      </div>
    );
  }
  
  if (currentUser.op !== 1) {
    return (
      <div className="max-w-2xl mx-auto py-8 text-center">
        <Card className="shadow-xl">
          <CardHeader><CardTitle className="text-2xl font-bold">无权访问</CardTitle></CardHeader>
          <CardContent className="space-y-4">
            <Alert variant="destructive">
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>权限不足</AlertTitle>
              <AlertDescription>您没有管理用户的权限。请联系管理员。</AlertDescription>
            </Alert>
            <Button onClick={() => { startLoading(); router.push('/dashboard'); }} className="w-full">
              <Home className="mr-2 h-4 w-4" /> 返回仪表盘
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (error && !isLoadingUsers && !isLoadingUser) { 
    return (
      <div className="container mx-auto px-4 py-8 text-center">
         <Alert variant="destructive" className="max-w-lg mx-auto">
            <ServerCrash className="h-5 w-5" />
            <AlertTitle>加载错误</AlertTitle>
            <AlertDescription>{error}</AlertDescription>
         </Alert>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <Card className="shadow-xl">
        <CardHeader className="flex flex-row items-center justify-between">
          <div>
            <CardTitle className="text-3xl font-bold flex items-center">
              <Users className="mr-3 h-7 w-7 text-primary" />
              用户管理
            </CardTitle>
            <CardDescription>查看、添加、编辑或删除系统用户。</CardDescription>
          </div>
          <Dialog open={isAddUserOpen} onOpenChange={setIsAddUserOpen}>
            <DialogTrigger asChild>
              <Button disabled={isAddUserPending || isUpdateUserPending || isDeleting}><UserPlus className="mr-2 h-4 w-4" /> 添加新用户</Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>添加新用户</DialogTitle>
                <DialogDescription>填写新用户的详细信息。</DialogDescription>
              </DialogHeader>
              <form action={addUserFormAction} className="space-y-4">
                <div className="space-y-1">
                  <Label htmlFor="add-minecraftUsername">Minecraft 用户名</Label>
                  <Input id="add-minecraftUsername" name="minecraftUsername" required disabled={isAddUserPending}/>
                  {addUserFormState.errors?.minecraftUsername && <p className="text-sm text-destructive">{addUserFormState.errors.minecraftUsername[0]}</p>}
                </div>
                <div className="space-y-1">
                  <Label htmlFor="add-avatarUrl">头像链接 (可选)</Label>
                  <Input id="add-avatarUrl" name="avatarUrl" type="url" placeholder="https://placehold.co/100x100.png" disabled={isAddUserPending}/>
                  {addUserFormState.errors?.avatarUrl && <p className="text-sm text-destructive">{addUserFormState.errors.avatarUrl[0]}</p>}
                </div>
                <div className="space-y-1">
                  <Label htmlFor="add-op">权限级别 (OP)</Label>
                   <Select name="op" defaultValue="0" disabled={isAddUserPending}>
                    <SelectTrigger id="add-op"><SelectValue placeholder="选择权限级别" /></SelectTrigger>
                    <SelectContent>
                      <SelectItem value="0">普通用户 (0)</SelectItem>
                      <SelectItem value="1">管理员 (1)</SelectItem>
                    </SelectContent>
                  </Select>
                  {addUserFormState.errors?.op && <p className="text-sm text-destructive">{addUserFormState.errors.op[0]}</p>}
                </div>
                <div className="space-y-1">
                  <Label htmlFor="add-password">密码</Label>
                  <Input id="add-password" name="password" type="password" required disabled={isAddUserPending}/>
                  {addUserFormState.errors?.password && <p className="text-sm text-destructive">{addUserFormState.errors.password[0]}</p>}
                </div>
                <div className="space-y-1">
                  <Label htmlFor="add-confirmPassword">确认密码</Label>
                  <Input id="add-confirmPassword" name="confirmPassword" type="password" required disabled={isAddUserPending}/>
                  {addUserFormState.errors?.confirmPassword && <p className="text-sm text-destructive">{addUserFormState.errors.confirmPassword[0]}</p>}
                </div>
                 <div className="space-y-1">
                  <Label htmlFor="add-adminConfirmPassword">您的账户密码 (用于授权)</Label>
                  <Input id="add-adminConfirmPassword" name="adminConfirmPassword" type="password" required disabled={isAddUserPending}/>
                  {addUserFormState.errors?.adminConfirmPassword && <p className="text-sm text-destructive">{addUserFormState.errors.adminConfirmPassword[0]}</p>}
                </div>
                {addUserFormState.message && !addUserFormState.success && addUserFormState.errors?.form && (
                    <p className="text-sm text-destructive flex items-center"><AlertTriangle className="w-4 h-4 mr-1"/> {addUserFormState.errors.form[0]}</p>
                )}
                 {addUserFormState.message && !addUserFormState.success && !addUserFormState.errors?.form && (
                    <p className="text-sm text-destructive flex items-center"><AlertTriangle className="w-4 h-4 mr-1"/> {addUserFormState.message}</p>
                )}
                <DialogFooter>
                  <DialogClose asChild><Button type="button" variant="outline" disabled={isAddUserPending}>取消</Button></DialogClose>
                  <DialogSubmitButton disabled={isAddUserPending}>创建用户</DialogSubmitButton>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>
        </CardHeader>
        <CardContent>
          {isLoadingUsers && ( 
            <div className="flex justify-center items-center py-10">
              <Loader2 className="h-10 w-10 animate-spin text-primary" />
              <p className="ml-3 text-muted-foreground">正在加载用户列表...</p>
            </div>
          )}
          {!isLoadingUsers && !error && users.length === 0 && (
            <Alert>
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>暂无用户</AlertTitle>
              <AlertDescription>系统中当前没有其他用户。</AlertDescription>
            </Alert>
          )}
          {!isLoadingUsers && !error && users.length > 0 && (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>头像</TableHead>
                    <TableHead>用户名</TableHead>
                    <TableHead>用户ID</TableHead>
                    <TableHead className="text-center">OP</TableHead>
                    <TableHead className="text-right">操作</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {users.map((user) => (
                    <TableRow key={user.id}>
                      <TableCell><UserAvatar user={user} className="h-8 w-8" /></TableCell>
                      <TableCell className="font-medium">{user.username}</TableCell>
                      <TableCell className="text-xs text-muted-foreground">{user.id}</TableCell>
                      <TableCell className="text-center">{user.op === 1 ? '是' : '否'}</TableCell>
                      <TableCell className="text-right space-x-2">
                        <Button variant="outline" size="sm" onClick={() => handleOpenEditDialog(user)} disabled={isAddUserPending || isUpdateUserPending || isDeleting}>
                          <Edit className="mr-1 h-3 w-3" /> 编辑
                        </Button>
                        <Button variant="destructive" size="sm" onClick={() => confirmDeleteUser(user)} disabled={currentUser?.id === user.id || isAddUserPending || isUpdateUserPending || isDeleting}>
                          <Trash2 className="mr-1 h-3 w-3" /> 删除
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>

      {userToEdit && (
        <Dialog open={isEditUserOpen} onOpenChange={(isOpen) => { setIsEditUserOpen(isOpen); if (!isOpen) setUserToEdit(null); }}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>编辑用户: {userToEdit.username}</DialogTitle>
              <DialogDescription>修改用户信息。留空密码字段则不更改密码。</DialogDescription>
            </DialogHeader>
            <form action={updateUserFormAction} className="space-y-4">
              <input type="hidden" name="userIdToUpdate" value={userToEdit.id} />
              <div className="space-y-1">
                <Label htmlFor="edit-minecraftUsername">Minecraft 用户名</Label>
                <Input id="edit-minecraftUsername" name="minecraftUsername" defaultValue={userToEdit.username} required disabled={isUpdateUserPending}/>
                {updateUserFormState.errors?.minecraftUsername && <p className="text-sm text-destructive">{updateUserFormState.errors.minecraftUsername[0]}</p>}
              </div>
              <div className="space-y-1">
                <Label htmlFor="edit-avatarUrl">头像链接 (可选)</Label>
                <Input id="edit-avatarUrl" name="avatarUrl" type="url" defaultValue={userToEdit.avatarUrl || ''} placeholder="https://placehold.co/100x100.png" disabled={isUpdateUserPending}/>
                {updateUserFormState.errors?.avatarUrl && <p className="text-sm text-destructive">{updateUserFormState.errors.avatarUrl[0]}</p>}
              </div>
              <div className="space-y-1">
                <Label htmlFor="edit-op">权限级别 (OP)</Label>
                 <Select name="op" defaultValue={userToEdit.op?.toString() || '0'} disabled={isUpdateUserPending || currentUser?.id === userToEdit.id }>
                  <SelectTrigger id="edit-op"><SelectValue placeholder="选择权限级别" /></SelectTrigger>
                  <SelectContent>
                    <SelectItem value="0">普通用户 (0)</SelectItem>
                    <SelectItem value="1">管理员 (1)</SelectItem>
                  </SelectContent>
                </Select>
                {updateUserFormState.errors?.op && <p className="text-sm text-destructive">{updateUserFormState.errors.op[0]}</p>}
              </div>
              <div className="space-y-1">
                <Label htmlFor="edit-newPassword">新密码 (可选 - 留空则不更改)</Label>
                <Input id="edit-newPassword" name="newPassword" type="password" disabled={isUpdateUserPending}/>
                {updateUserFormState.errors?.newPassword && <p className="text-sm text-destructive">{updateUserFormState.errors.newPassword[0]}</p>}
              </div>
              <div className="space-y-1">
                <Label htmlFor="edit-confirmNewPassword">确认新密码 (如果设置了新密码)</Label>
                <Input id="edit-confirmNewPassword" name="confirmNewPassword" type="password" disabled={isUpdateUserPending}/>
                {updateUserFormState.errors?.confirmNewPassword && <p className="text-sm text-destructive">{updateUserFormState.errors.confirmNewPassword[0]}</p>}
              </div>
               <div className="space-y-1">
                <Label htmlFor="edit-adminConfirmPassword">您的账户密码 (用于授权)</Label>
                <Input id="edit-adminConfirmPassword" name="adminConfirmPassword" type="password" required disabled={isUpdateUserPending}/>
                {updateUserFormState.errors?.adminConfirmPassword && <p className="text-sm text-destructive">{updateUserFormState.errors.adminConfirmPassword[0]}</p>}
              </div>
              {updateUserFormState.message && !updateUserFormState.success && updateUserFormState.errors?.form &&(
                  <p className="text-sm text-destructive flex items-center"><AlertTriangle className="w-4 h-4 mr-1"/> {updateUserFormState.errors.form[0]}</p>
              )}
               {updateUserFormState.message && !updateUserFormState.success && !updateUserFormState.errors?.form &&(
                  <p className="text-sm text-destructive flex items-center"><AlertTriangle className="w-4 h-4 mr-1"/> {updateUserFormState.message}</p>
              )}
              <DialogFooter>
                <DialogClose asChild><Button type="button" variant="outline" disabled={isUpdateUserPending}>取消</Button></DialogClose>
                <DialogSubmitButton disabled={isUpdateUserPending}>保存更改</DialogSubmitButton>
              </DialogFooter>
            </form>
          </DialogContent>
        </Dialog>
      )}

      {userToDelete && (
        <AlertDialog open={showDeleteDialog} onOpenChange={setShowDeleteDialog}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle className="flex items-center">
                <ShieldAlert className="mr-2 h-6 w-6 text-destructive"/>
                确认删除用户: {userToDelete.username}?
              </AlertDialogTitle>
              <AlertDialogDescription>
                此操作无法撤销。这将永久删除该用户。如果该用户是任何活动的组织者，删除将被阻止。请输入您的账户密码以确认。
              </AlertDialogDescription>
            </AlertDialogHeader>
            <div className="space-y-2 mt-4">
              <Label htmlFor="adminPasswordForDelete">您的账户密码</Label>
              <Input
                id="adminPasswordForDelete"
                type="password"
                value={adminPasswordForDelete}
                onChange={(e) => setAdminPasswordForDelete(e.target.value)}
                placeholder="输入密码以确认"
                disabled={isDeleting}
              />
              {deleteError && <p className="text-sm text-destructive">{deleteError}</p>}
            </div>
            <AlertDialogFooter className="mt-6">
              <AlertDialogCancel disabled={isDeleting} onClick={() => setShowDeleteDialog(false)}>取消</AlertDialogCancel>
              <AlertDialogAction
                onClick={executeDeleteUser}
                disabled={isDeleting || !adminPasswordForDelete || isAddUserPending || isUpdateUserPending}
                className={buttonVariants({ variant: "destructive" })}
              >
                {isDeleting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Trash2 className="mr-2 h-4 w-4" />}
                确认删除
              </AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      )}
    </div>
  );
}

